import * as React from 'react';
import * as style from './right.module.css';
import Experience from '../components/experience';
import Award from '../components/award';
import Project from '../components/project';
import Subtitle from '../components/subtitle';

const Right = () => {
    const a = React.useRef(null)
    const anchorTo = () => {
        console.log(a.current.offsetTop);
        document.body.scrollTop = a.current.offsetTop;
        document.documentElement.scrollTop = a.current.offsetTop;
        window.pageYOffset = a.current.offsetTop;
    }
    return (
        <div className={style.rightcontainer}>
            <p onClick={anchorTo}>exp</p>
            <Experience id="anchorlink"/>
            <Project className="anchorlink" />
            <Subtitle title="anchor" ref={a} />
            <Award className="anchorlink" />
        </div>
    )
}

export default Right